.ready-container {
    position: relative;
}


.ready-box2 {
    position: absolute;
    top: rem(92px);
    left: rem(156px);
}

.ready-box2 h1 {
    font-family: spartan-bold;
    font-size: rem(40px);
    line-height: rem(56px);
    letter-spacing: rem(-0.5px);
    color: #ffffff;
}

.ready-box2 a {
    position: absolute;
    left: rem(950px);
}

.ready-box2 a button {
    color: #ffffff;
    width: rem(245px);
    height: rem(64px);
    border: 1px solid #fff;
    background-color: rgba(0, 128, 0, 0);
    font-size: rem(14px);
    font-family: spartan;
    line-height: rem(16px);
    letter-spacing: rem(2px);
    cursor: pointer;
    transition: 0.5s;
}

.ready-box2 a  :hover {
    background-color: #ffffff;
    color: #000000;
}


@include for-narrow-layout {
   
   
 }
 
 @include for-mobile-layout {
     .ready-container {
        height: rem(328px);
     }

     .ready-img {
        content: url('../../assets/imgs/homepage/ready-bg-mobile.jpg');  
       }

       .ready-box2 {
           flex-direction: column-reverse;
           text-align: center;
           left: 0px;

       }
  
       .ready-box2 a {
        left: 0px;
        width: 100%;
        margin: 0 auto;
        top: rem(136px);
       }
 }
 
