.calander-container {
    position: relative;
}


.calander-box1 :nth-child(2) {
    position: absolute;
    top: rem(166px);
    right: rem(706px);
}


.order-form {
    position: absolute;
    right: rem(250px);
    bottom: rem(120px);
    width: rem(540px);
    height: rem(545px);
    background-color: #fff;
    text-align: center;
    border: 1px hidden #111;
    //border-style:hidden;
}

.order-form>* {
    max-width: rem(445px);
    max-height: rem(45px);
    margin: 0 auto;
    top: rem(48px);
}

.form-field {
    max-width: rem(445px);
    max-height: rem(45px);
    position:absolute;
    left: rem(45px);
}

.email {
    top: rem(82px);
}

.pick-date, .pick-time {
    width: rem(445px);
    height: rem(45px);
    position: relative;
}

.pick-date {
    top: rem(100px);
}

.pick-time {
    top: rem(120px);
}

.date-field {
    max-width: rem(80px) !important;
    max-height: rem(45px) !important;
    position: relative !important;
    left: rem(160px) !important;
}

label {
    font-family: spartan;
    font-size: rem(18px);
    line-height: rem(28px);
    letter-spacing: rem(-0.22px);
    position: absolute;
    top: rem(20px);
}

.df1, .df2, .df3 {
   margin-left: rem(10px) !important;
   width: 80px ;
}

.df33 {
    width: rem(80px) !important;
   margin-left: rem(10px) !important;
  position: relative;
  top: 3px;
  margin-top: rem(15px);
}

.people {
    top: rem(150px);
    text-align: center;
}

.minus {
    position: relative;
    left: rem(32px);
    cursor: pointer;
}

.plus {
    position: relative;
    right: rem(32px);
    cursor: pointer;
}

.submmit-btn {
    position: relative;
    top: rem(200px);
    width: rem(444px);
    min-height: rem(74px);
    font-family: spartan-light;
    font-size: rem(14px);
    line-height: rem(28px);
    letter-spacing: rem(2px);
    text-align: center;
    background-color: #111111;
    color: #ffffff;
    cursor: pointer;
    transition: 0.5s;
}

.submmit-btn:hover {
    background-color: #ffffff;
    color: #000000;
    border: 1px solid #000000;
}


@include for-narrow-layout {
    .calander-box1 {
        height: rem(500px);
    }

   .calander-box1 img {
       display: none;
   }
}

@include for-mobile-layout {
  .order-form {
      //border: 1px solid red;
      width: rem(327px);
      height: rem(585px);
      bottom: rem(120px);
      left: rem(40px);
  }

  .form-field {
      width: 263px;
  }

//   .pick-date {
//       position: relative;
//       right: 100px;
//       border: 1px solid red;
//       max-width: 500px;
//   }


  .date-field {
    // max-width: rem(80px) !important;
    // max-height: rem(45px) !important;
    // position: relative !important;
     left: rem(120px) !important;
}

  .submmit-btn {
      width: rem(263px);
  }

  .pick-date, .pick-time {
      width: 150px;
      left: -60px;
  }
  .people {
      top: rem(150px);
      left: rem(30px);
  }
   
}
